
.tagsContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  .welcome{
    display:block;
    width: 100%;
    font-size: 40px !important;
    color: white;
    padding: 10px;
  }
  .tag {
    display: inline-block;
    flex: 1 1 10%;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    background: linear-gradient(90deg, rgba(0,90,158,1) 0%, rgba(22,106,169,1) 35%, rgba(36,112,169,1) 100%);
    color: #ffffff;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    text-align: center;
    width: 200px !important;
    height: 170px;
    padding-top: 10px;
  }
  
  .tag:nth-child(1) {
    background: linear-gradient(90deg, rgba(0,90,158,1) 0%, rgba(22,106,169,1) 35%, rgba(36,112,169,1) 100%);
    
  }
  
  .tag:nth-child(2) {
    /* background-color: #e81123; */
    background: linear-gradient(90deg, rgba(251,99,99,1) 0%, rgba(250,122,122,1) 35%, rgba(249,120,120,1) 100%);

  }
  
  .tag:nth-child(3) {
    background: linear-gradient(90deg, rgba(80,162,80,1) 0%, rgba(111,185,86,1) 35%, rgba(117,189,98,1) 100%);
  }
  
  .tag:nth-child(4) {
    background: linear-gradient(90deg, rgba(255,185,0,1) 0%, rgba(240,187,49,1) 35%, rgba(248,207,101,1) 100%);
  }
  
  .tag:nth-child(5) {
    background: linear-gradient(90deg, rgba(92,45,145,1) 0%, rgba(114,77,156,1) 35%, rgba(112,80,149,1) 100%);
  }
  
  .tag:hover:nth-child(1) {
    background-color: #005a9e;
  }
  
  .tag:hover:nth-child(2) {
    background-color: #c50f1f;
  }
  
  .tag:hover:nth-child(3) {
    background-color: #0b6a0b;
  }
  
  .tag:hover:nth-child(4) {
    background-color: #eaa300;
  }
  
  .tag:hover:nth-child(5) {
    background-color: #4b217a;
  }
  
  .tag:hover {
    background-color: #005a9e;
  }
  
  /* Media Queries for Responsiveness */
  @media (max-width: 1200px) {
    .tag {
      flex: 1 1 30%;
    }
  }
  
  @media (max-width: 768px) {
    .tag {
      flex: 1 1 45%;
    }
  }
  
  @media (max-width: 480px) {
    .tag {
      flex: 1 1 100%;
    }
  }

  .loaderContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(122, 119, 119, 0.5);
    z-index: 1000;
  }
  
  .loader {
    width: 60px;
    height: 60px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #262262;
    border-radius: 70%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  .b_title
{
  color: white !important;
}